import { Helmet } from "react-helmet";
import * as React from "react"
import Header from '../components/Header'

import Helmet1 from '../components/Helmet'
import Transition from '../components/Transition'
import { Link } from "gatsby"
import dotsw from '../../static/Anindya Biswas white dots.png'
import dotsb from '../../static/Anindya Biswas black dots.png'

import $ from 'jquery';
import { faArrowDown, faArrowRight } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { useState, useEffect } from 'react';



function view(){
  document.getElementById("hi").scrollIntoView({behavior:'smooth'});
}

export default function Home() {
  const [showTransition, setShowTransition] = useState(false);
   const [isClicked, setIsClicked] = useState(false);

   const handleClick = () => {
    setIsClicked((prev) => !prev); // Toggle the clicked state
  };

  useEffect(() => {
    // Check if navigation is forward (not back)
    const navigationType = window.performance.getEntriesByType('navigation')[0]?.type;
    if (navigationType === 'navigate' || navigationType === 'reload') {
      setShowTransition(true); // Show animation only on forward or reload
      const timer = setTimeout(() => setShowTransition(false), 1000);
      return () => clearTimeout(timer);
    }
  }, []);


  if(typeof window !== 'undefined'){
  window.addEventListener("scroll",function(){
    var pageBottom = window.scrollY + window.screen.height;
    var tags = $(".appear");
    var elements = document.getElementsByClassName("appear");

    for (var i = 0; i < tags.length; i++) {

      var tag = tags[i];

    if (elements[i].offsetTop < pageBottom) 
      {
        $(tag).addClass("visible");
      } 
      else
      {
        $(tag).removeClass("visible");

      }
    }

  });
  return(
  <>
{showTransition && <Transition />}
<Helmet>
      <title>Anindya Biswas, IIT Roorkee M.Tech AI | UCSTA IT</title>
      <meta name="description" content="Official portfolio of Anindya Biswas, M.Tech AI IIT Roorkee (IITR), showcasing projects, skills, and achievements in AI, ML, LLM, RAG and software development." />
        <meta name="keywords" content="Anindya Biswas, IIT Roorkee, IITR, MFSDSAI, Mehta Family School of DS and AI, UCSTA, University of Calcutta, Tech Campus, AKCSIT, Amlan Chakrabarti, Durga Toshinwal, Soumya Sen, Wazib Ansar, LLM, RAG, Cohere, OpenAI, portfolio, AI projects, software development, developer, Kolkata, West Bengal, Birati, Ajodhyanagar" />
        <meta name="author" content="Anindya Biswas" />
</Helmet> 
<Header>
   <Helmet1></Helmet1>
    <div className={`flex flex-wrap items-center align-middle justify-evenly h-dvh dark:bg-neutral-800 bg-neutral-200 duration-500  ${isClicked ? "bg-red-500 dark:bg-red-500 duration-1000" : "dark:bg-neutral-800 bg-neutral-200"}`}>
      <img src={dotsw} id="bg" alt="Anindya Biswas" className=" absolute object-cover h-full rotate max-sm:h-2/3 opacity-60 hidden visible dark:block"/>
      <img src={dotsb} id="bg" alt="Anindya Biswas" className=" absolute object-cover h-full rotate max-sm:h-2/3 opacity-60  dark:hidden"/>
      <h1 className={`text-5xl max-sm:text-2xl py-4  dark:bg-neutral-800 bg-neutral-200 text-neutral-400 w-full text-center fade-up z-0 duration-500 ${isClicked ? "bg-red-500 dark:bg-red-500 duration-1000 text-red-200" : "dark:bg-neutral-800 bg-neutral-200"}`}>
        Welcome to&nbsp;
        <button className="rounded-full text-white duration-500 hover:text-red-600 hover:bg-transparent hover:px-1 "  onClick={handleClick}>Anindya's</button> Home!</h1>
        <div className="bg-red-600 text-white font-bold px-6 py-3 rounded-full absolute bottom-12 lg:right-12 hover:bg-red-500 duration-500 z-1" onClick={view}><FontAwesomeIcon icon={faArrowDown}/></div>

    </div>


    <div className="flex flex-wrap items-center align-middle justify-evenly bg-gray-50 dark:bg-neutral-900 min-h-screen max-h-fit">
     
      <div className=" p-10 pb-1 rounded-3xl appear dark:text-neutral-200" id="slide2">
        <h1 className=" text-6xl mb-5" id="hi">Hi there! üòÅ</h1>
        <p className="mb-2 ms-2 text-lg opacity-70"> It's Anindya Biswas and this is my online home.</p>
        <p className="mb-1 ms-2 text-lg opacity-70"> I'm currently pursuing my Master's in AI at IITR while exploring the world through fun, creative projects.</p>
        <p className="mb-14 ms-2 text-lg opacity-70">Curious to know more? Head over to About, and don't forget to check out my Works!</p>
        <div className="flex flex-wrap items-center align-middle">

        <Link to="/works" className=" text-2xl rounded-full py-4 px-7 m-2 text-red-100 bg-red-600 hover:bg-red-500 hover:ease-in-out duration-300">Works <FontAwesomeIcon icon={faArrowRight} size="xs" className=" ms-2"/></Link>
        <Link to="/about" className=" text-2xl rounded-full py-4 px-7 m-2 text-red-900 hover:text-red-100 border border-red-600 hover:bg-red-600 hover:ease-in-out duration-300 dark:text-red-100">About <FontAwesomeIcon icon={faArrowRight} size="xs" className=" ms-2"/></Link>
        </div>
      </div>
    </div>

    </Header>
    </>
  )

}
}

